/*Main CSS*/
@media only screen and (max-device-width: 767px) {
    .login-logo img {
        margin: 0 auto;
    }
    .login-details .nav-tabs > li {
        text-align: center;
        width: 50%;
    }
    .login-signup .login-inner h1 {
        font-size: 26px;
        margin-bottom: 0;
        margin-top: 10px;
    }
    .login-inner .login-form input {
        font-size: 15px;
        max-width: 100%;
        padding: 15px 45px;
    }
    .login-inner .form-details {
        padding: 25px;
    }
    .login-inner .login-form label {
        margin-bottom: 20px;
        width: 100%;
    }
    .login-inner .form-btn {
        margin: 0;
        max-width: 180px;
    }
    .tab-content .tab-pane {
        padding: 20px 0;
    }
    #navigation .navi a {
        font-size: 14px;
        padding: 20px;
        text-align: center;
    }
    #navigation .navi i {
        margin-right: 0px;
    }
    #navigation .navi a:hover,
    #navigation .navi .active a {
        background: #122143 none repeat scroll 0 0;
        border-left: none;
        display: block;
        padding-left: 20px;
    }
    header .header-top img {
        max-width: 38px !important;
    }
    .v-align header {
        padding: 12px 15px;
    }
    header .header-top li {
        padding-left: 13px;
        padding-right: 6px;
    }
    .navbar-default .navbar-toggle {
        border-color: transparent;
    }

    .navbar-toggle:focus{
        border-color: #0E1A35;
    }

    button,
        html [type="button"],
        [type="reset"],
        [type="submit"] {
            outline: medium none;
    }
    .user-dashboard .sales h2 {
        color: #8492af;
        float: left;
        font-size: 14px;
        font-weight: 600;
        margin: 0;
        padding: 13px 0 0;
    }
        .user-dashboard .btn.btn-secondary.btn-lg.dropdown-toggle > span {
            font-size: 11px;
    }
        .user-dashboard .sales button {
            font-size: 11px;
            padding-right: 23px;
    }  
        .user-dashboard .sales h2 {
        font-size: 12px;
    }
    .gutter{
        padding: 0;
    }
    
    .visible-xs {
        display: block!important;
    }

    .modal-main {
        width: 80%;
    }

    .modal-main th{
        
        font-size: 11px;
        
    }
}

@media only screen and (max-device-width: 992px) {
    header .header-top li {
        padding-left: 20px !important;
        padding-right: 0;
    }
        header .logo img {
            max-width: 125px !important;
    }

}

@media only screen and (min-device-width: 767px) and (max-device-width: 998px){
      .user-dashboard .header-top {
        padding-top: 5px;
    }
        .user-dashboard .header-rightside {
            display: inline-block;
            width: 100%;
    }
        .user-dashboard .header-rightside .header-top img {
            max-width: 41px !important;
    } 
        .user-dashboard .sales button {
        font-size: 10px;
    }
        .user-dashboard .btn.btn-secondary.btn-lg.dropdown-toggle > span {
        font-size: 12px;
    }
        .user-dashboard .sales h2 {
        font-size: 15px;
    }
}


@media only screen and (min-device-width:998px) and (max-device-width: 1350px){
    #navigation .logo img {
    max-width: 130px;
    padding: 16px 0 17px;
    width: 100%;
    }
}

@media (min-width:768px){
    .navbar-default{
        display: none;
    }
}

.navbar-toggle{
    background-color: transparent;
}

